.cart {
  @media (min-width: $xl) {
    display: flex;
    gap: 30px;
  }

  &-bg-block {
    padding: 15px;
    background-color: $white;

    @media (min-width: $md) {
      padding: 20px;
    }
  }

  &__block {
    @media (min-width: $xl) {
      flex: 1 1 68%;
    }

    &-heading,
    &-order,
    &-empty {
      margin-bottom: 15px;

      @media (min-width: $xl) {
        margin-bottom: 30px;
      }
    }

    &-heading {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 10px;
      font-weight: 600;
      color: $gray;
    }

    &-empty {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 400px;
    }
  }

  &__multi-remove {
    display: flex;
    align-items: center;
    gap: 10px;
    color: $gray;
    line-height: 1;

    @media (any-hover: hover) {
      &:hover .cart__multi-remove-icon {
        transform: rotate(90deg);
      }
    }

    &-icon {
      width: 10px;
      height: 10px;
      transition: $transition;
    }
  }

  &__delivery {
    @media (max-width: $max-xl) {
      display: none;
    }

    @media (min-width: $xl) {
      flex: 1 1 32%;
    }
  }

  &-order {
    &__item {
      position: relative;
      display: flex;
      gap: 10px;

      @media (min-width: $md) {
        gap: 30px;
      }

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }

    &__remove {
      position: absolute;
      top: 15px;
      right: 15px;
      cursor: pointer;
      transition: $transition;

      @media (min-width: $md) {
        top: 20px;
        right: 20px;
      }

      @media (any-hover: hover) {
        &:hover {
          color: $secondary-clr;
          transform: rotate(90deg);
        }
      }
    }

    &__img {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 60px;
      width: 60px;
      height: 60px;
      border: 1px solid $bg-clr;

      @media (min-width: $xs) {
        flex-basis: 80px;
        height: 80px;
      }

      @media (min-width: $md) {
        flex-basis: 150px;
        height: 150px;
      }

      img {
        width: 35px;
        height: 35px;

        @media (min-width: $md) {
          width: 70px;
          height: 70px;
        }
      }
    }

    &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex: 1 1 100%;
    }

    &__title {
      padding-right: 20px;

      @media (max-width: $max-xs) {
        font-size: 16px;
      }

      &-price {
        @media (max-width: $max-md) {
          display: none;
        }

        @media (min-width: $md) {
          display: block;
          margin-top: 10px;
          font-size: 14px;
        }

        @media (min-width: $xl) {
          display: none;
        }
      }

      &-sku {
        @media (max-width: $max-xl) {
          display: none;
        }

        @media (min-width: $xl) {
          display: block;
          margin-bottom: 15px;
          font-size: 12px;
          font-weight: 600;
          color: $gray;
        }
      }
    }

    &__details {
      @media (min-width: $xs) {
        display: flex;
        align-items: center;
        gap: 15px;
        line-height: 1;
      }

      @media (min-width: $md) {
        justify-content: space-between;
      }
    }

    &__price {
      @media (max-width: $max-xs) {
        font-size: 10px;
      }

      @media (min-width: $md) {
        display: none;
      }

      @media (min-width: $xl) {
        display: block;
        order: 1;
      }
    }

    &__size {
      @media (min-width: $md) {
        font-size: 12px;
      }

      @media (min-width: $xl) {
        font-size: 18px;
        font-family: $font-txt;
      }
    }

    &__sku {
      @media (min-width: $md) {
        font-weight: 600;
      }

      @media (min-width: $xl) {
        display: none;
      }
    }

    &__size,
    &__sku {
      font-family: $font-title;
      color: $gray;

      @media (max-width: $max-xs) {
        font-size: 8px;
      }

      @media (min-width: $xs) {
        font-size: 10px;
      }

      @media (min-width: $xl) {
        color: $txt-clr;
      }
    }
  }
}
